import { useEffect, useState, useContext } from "react";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import "../../index.css";

import VideosListAd from "../admin/videosListAdmin";
import { ErrorBoundary } from "./errorsBoundary";
import { ListCategorias } from "../../services/category";
import SearchComponent from "./search";
import Context from "../context/UserContext";
import VideosListUser from "./videoListUser";

const VideosList = ({ videos }) => {
  const [query, setQuery] = useState("");
  const [categories, setCategories] = useState([]);
  const [filteredVideos, setFilteredVideos] = useState([]);
  const [filterParam, setFilterParam] = useState("All");
  const [loading, setLoading] = useState(true); // ðŸ‘ˆ nuevo estado para estabilizar renderizado

  const { user } = useContext(Context);

  useEffect(() => {
    const fetchCategorias = async () => {
      try {
        const res = await ListCategorias();
        setCategories(res);
      } catch (error) {
        console.log("Error al cargar categorÃ­as");
      }
    };
    fetchCategorias();
  }, []);

  const removeAccents = (str) =>
    str.normalize("NFD").replace(/[\u0300-\u036f]/g, "");

  const search = (videos) => {
    if (!query) return videos;

    const queryLower = removeAccents(query.toLowerCase());

    return videos.filter((dato) => {
      const titleMatches = removeAccents(dato.title_espanol || "")
        .toLowerCase()
        .includes(queryLower);

      const descriptionMatches = removeAccents(dato.description_esp || "")
        .toLowerCase()
        .includes(queryLower);

      const categoryMatches = dato.categorias?.some((c) =>
        removeAccents(c.categoria.toLowerCase()).includes(queryLower)
      );

      const especialidadMatches = dato.especialidad?.some((e) =>
        removeAccents(e.especialidad.toLowerCase()).includes(queryLower)
      );

      const subEspecialidadMatches = dato.subEspecialidad?.some((e) =>
        removeAccents(e.subEspecialidad.toLowerCase()).includes(queryLower)
      );

      const palabraClaveMatches = dato.palabraClave?.some((e) =>
        removeAccents(e.palabraClave.toLowerCase()).includes(queryLower)
      );

      const autorMatches = dato.autor?.some((e) =>
        removeAccents(e.autor.toLowerCase()).includes(queryLower)
      );

      return (
        titleMatches ||
        descriptionMatches ||
        categoryMatches ||
        especialidadMatches ||
        subEspecialidadMatches ||
        palabraClaveMatches ||
        autorMatches
      );
    });
  };

  const search2 = (categories) => {
    const contenedorCarousel = document.getElementById("carousel"); // âš ï¸ cuidado con esto

    return categories.filter((item) => {
      if (!contenedorCarousel) {
        console.warn("No se encontrÃ³ el contenedor del carrusel");
        return false;
      }

      if (item.categoria === filterParam) {
        contenedorCarousel.style.visibility = "hidden";
        contenedorCarousel.style.height = "10px";

        return removeAccents(item.categoria.toLowerCase()).includes(
          removeAccents(query.toLowerCase())
        );
      } else if (filterParam === "All") {
        contenedorCarousel.style.visibility = "visible";
        contenedorCarousel.style.height = "100%";
        return true;
      }

      return false;
    });
  };

  useEffect(() => {
    // Solo actualiza si hay videos disponibles
    if (Array.isArray(videos)) {
      const results = search(videos);
      setFilteredVideos(results);
      setLoading(false); // âœ… Ya se puede mostrar resultados
    }
  }, [query, videos]);

  const renderList = () => {
    if (loading) {
      return <h1 className="m-5 text-center">Cargando videos...</h1>;
    }

    if (filteredVideos.length === 0) {
      return (
        <h1 className="m-5 text-center">No se encontraron videos</h1>
      );
    }

    const Component = user?.is_superuser ? VideosListAd : VideosListUser;

    return (
      <Component
        videos={filteredVideos}
        categories={categories}
        search={search}
        search2={search2}
      />
    );
  };

  return (
    <div>
      <SearchComponent
        query={query}
        setQuery={setQuery}
        filterParam={filterParam}
        setFilterParam={setFilterParam}
        categories={categories}
      />
      <ErrorBoundary>{renderList()}</ErrorBoundary>
    </div>
  );
};

export default VideosList;
